* {
    margin: 0px;
    padding: 0px;
}

@font-face {
    font-family: "ChunkFive";
    src: url("../fonts/ChunkFive-Regular.otf");
}

@font-face {
    font-family: "IBM Plex Mono";
    src: url("../fonts/IBM_Plex_Mono/IBMPlexMono-Regular.ttf");
}

body {
    background-color: rgb(245, 245, 245);
}

.mainBox {
    position: relative;
    width: 390px;
    height: 700px;
    /* overflow-y: scroll;  */

    margin: 10px auto;

    background: #FFFFFF;
    border-radius: 20px;
}

.headBack {

    position: fixed;
    width: 1090px;
    height: 58px;
    margin: 0px auto;
    background-color: rgb(245, 245, 245);

}

.headLine {
    /* Rectangle 5282 */


    position: fixed;
    width: 390px;
    height: 58px;

    border-top-left-radius: 20px;
    border-top-right-radius: 20px;

    background: #000000;
}

.headLeftButton {
    /* button.comp */


    /* Auto layout */

    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 0px;
    gap: 8px;
    line-height: 58px;

    position: absolute;
    width: 58px;
    height: 14px;
    left: 19px;
    top: 20px;
}

.headLine .title {
    /* title.block */


    /* Auto layout */

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0px auto;
    padding: 16px 0 0 0;
    gap: 16px;

    /* title.txt */


    width: 130px;
    height: 24px;

    font-family: 'IBM Plex Mono';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    /* identical to box height, or 100% */

    text-transform: uppercase;

    /* Greyscale/ W */

    color: #FFFFFF;


    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;


}

.personIcon {
    /* Person */


    position: absolute;
    width: 24px;
    height: 24px;
    left: 329px;
    top: 16px;
}

.notice{
    position:absolute;
    top: 65px;
    color:#000000;
    font-size:12px;
    text-indent: 2em;

}
.keyboard {
    position: relative;


    width: 370px;
    height: 100px;
    left: -30px;
    top: -310px;
    display: block;

}

.keyboard .line1 {
    position: relative;
    width: 380px;
    margin: 0 auto;
}

.keyboard .line2 {
    position: relative;
    width: 370px;
    margin: 0 auto;
}

.keyboard .line3 {
    position: relative;
    width: 340px;
    margin: 0 auto;
}

.keyboard .line4 {
    position: relative;
    width: 270px;
    margin: 0px auto;
}

.keyboard .button {


    position: relative;
    float: left;
    width: 30px;
    height: 46px;
    left: 45px;
    top: 500px;

    cursor: pointer;

    margin-top: 7px;
    margin-bottom: 7px;
    margin-left: 3px;
    margin-right: 3px;


    /* background: linear-gradient(180deg, #ff9422 0%, #a5bbdd 100%); */
    background-color: rgb(221, 221, 221);


    border-radius: 8px;

    /* transition: 0.2s;  */
    font-size: 14px;
    font-family: "ChunkFive";
    line-height: 46px;
    text-decoration: none;
    text-align: center;


}

