    /* margin-top:6px;
margin-bottom: 6px;
margin-left: 2px;
margin-right: 2px; */

    font-size: 18px;





}

.keyboard .longButton {
    position: relative;
    float: left;
    width: 230px;
    height: 46px;
    left: 105px;
    top: 500px;

    cursor: pointer;
    margin-top: 7px;
    margin-bottom: 7px;
    margin-left: 3px;
    margin-right: 3px;


    /* background: linear-gradient(180deg, #ff9422 0%, #a5bbdd 100%); */
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0);

    border-radius: 8px;

    transition: 0.3s ease-in-out;
    font-size: 18px;
    font-family: "ChunkFive";
    line-height: 46px;
    text-decoration: none;
    text-align: center;

}

.keyboard .longButton:hover {

    border: 2px solid rgba(0, 0, 0);
    margin-top: 6px;
    margin-bottom: 6px;
    margin-left: 2px;
    margin-right: 2px;

    font-size: 20px;

}



.button #generate {

    position: absolute;
    margin: 5px auto;
    width: 5px;
    height: 10px;

    font-size: 10px;
    text-decoration: none;
    text-align: center;

    color: black;

}

.canvasBox {

    position: relative;

    width: 200px;
    height: 200px;
    left: 45px;
    top: 60px;


}

.backPic {

    display: none;
    /* Left_Arrow */
    position: absolute;
    width: 48px;
    height: 48px;
    left: 327px;
    top: 90px;

}

.filePic1 {
    /* 查看图纸 */

    display: none;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 8px;

    position: relative;
    width: 101px;
    height: 127px;
    margin:340px auto;

    cursor: pointer;

}

.filePic1_2 {
    /* 下载图纸 */

    display: none;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 8px;

    position: absolute;
    width: 101px;
    height: 127px;
    left: calc(50% - 101px/2 - 99.5px);
    top: calc(50% - 127px/2 + 254.5px);

    cursor: pointer;

}

.filePic2 {
    /* 发送给ta */

    display: none;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 8px;

    position: absolute;
    width: 101px;
    height: 127px;
    left: calc(50% - 101px/2 + 86.5px);
    top: calc(50% - 127px/2 + 254.5px);

    cursor: pointer;

}

